.auth__ {
    &main {
        align-items: center;
        background-color: $primary;
        display: flex;
        height: 100vh;
        justify-content: center;
        width: 100vw;
    }


    &box-container {
        background-color: white;
        border-radius: 8px;
        box-shadow: 0px 3px $dark-grey;
        padding: 20px;
        width: 40vw;

        @media screen and (max-width: $medium) {
            width: 70vw;
        }
    }

    &title {
        color: darken($color: $primary, $amount: 20);
        font-size: 1.5em;
        font-weight: 600;
        text-align: center;
    }

    &input {
        border: 0px;
        border-bottom: 1px solid $ligth-grey;
        color: $dark-grey;
        font-size: 1.5em;
        width: 100%;

        transition: border-bottom .5s ease;

        &:focus {
            border-bottom: 1px solid $primary;
            outline: none;
        }
    }

    &social-networks {
        align-items: center;
        display: flex;
        justify-content: center;
        flex-direction: column;
        padding: 20px 0px;
    }

    &alert-error {
        background-color: red;
        border-radius: 5px;
        color: white;
        display: flex;
        justify-content: center;
        margin-bottom: 10px;
        padding: 5px;
    }

    &no-account {
        display: flex;
        justify-content: center;
        a {
            text-decoration: none;
        }
    }
}